"use client";

import { useEffect, useRef } from "react";
import { useTheme } from "next-themes";

declare global {
  interface Window {
    FB: {
      XFBML: {
        parse: () => void;
      };
    };
  }
}

interface FacebookCommentsProps {
  url: string;
}

export function FacebookComments({ url }: FacebookCommentsProps) {
  const { theme } = useTheme();
  const loadedRef = useRef(false);

  useEffect(() => {
    // 1. Add the fb-root div if it doesn't exist
    if (!document.getElementById("fb-root")) {
      const fbRoot = document.createElement("div");
      fbRoot.id = "fb-root";
      document.body.appendChild(fbRoot);
    }

    // 2. Load the SDK only once
    if (!loadedRef.current && !window.FB) {
      const script = document.createElement("script");
      script.src = "https://connect.facebook.net/en_US/sdk.js#xfbml=1&version=v24.0";
      script.async = true;
      script.defer = true;
      script.crossOrigin = "anonymous";
      script.nonce = "8j2h1k3l"; // Optional: Add a nonce if you have a Content Security Policy
      document.body.appendChild(script);
      loadedRef.current = true;
    } else if (window.FB) {
      // If SDK is already loaded, re-parse the DOM to render the plugin
      window.FB.XFBML.parse();
    }
  }, [url, theme]); // Re-run if URL or theme changes

  // Key prop forces React to remount the component when theme changes, 
  // ensuring the correct color scheme is applied by the plugin on render.
  return (
    <div className="mt-12 w-full overflow-hidden">
      <h3 className="text-2xl font-bold mb-8">Comments</h3>
      <div 
        className="fb-comments" 
        data-href={url} 
        data-width="100%" 
        data-numposts="5" 
        data-colorscheme={theme === "dark" ? "dark" : "light"}
        key={`${url}-${theme}`} 
      />
    </div>
  );
}
